*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif;
  font-size: 13px;
  color: #666;
  background: #fff; }

.container .panels {
  position: relative;
  height: 400px;
  width: 300px;
  overflow: hidden; }
  .container .panels.panel_basis .basis {
    margin-top: 0; }
  .container .panels.panel_advanced .basis {
    margin-top: -400px; }

.container .not_live {
  position: relative;
  height: 50px;
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: #23ade5; }
  .container .not_live img {
    margin-right: 10px; }

.basis {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  margin-top: 0;
  transition: all 0.3s ease; }
  .basis .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 40px;
    padding: 0 10px;
    background: #f1f1f1; }
    .basis .header .title {
      display: flex;
      align-items: center;
      text-decoration: none;
      color: #444; }
      .basis .header .title img {
        margin-right: 10px; }
    .basis .header .feedback {
      text-decoration: none;
      color: #888; }
  .basis .main {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-top: 1px solid #e6e6e6; }
    .basis .main .btn {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 120px;
      height: 120px;
      border-radius: 50%;
      cursor: pointer;
      transition: all 0.3s ease;
      border-width: 15px;
      border-style: solid;
      user-select: none;
      margin-bottom: 15px;
      color: #fff;
      transform: scale3d(1, 1, 1);
      text-shadow: 0 1px 0 rgba(0, 0, 0, 0.2); }
      .basis .main .btn:hover {
        opacity: 0.8;
        border-width: 20px;
        transform: scale3d(1.2, 1.2, 1.2); }
      .basis .main .btn:active {
        transform: scale3d(0.8, 0.8, 0.8); }
      .basis .main .btn.startRecord {
        background: #ff1b1b;
        border-color: #ffa5a5; }
      .basis .main .btn.stopRecord {
        background: #fb7299;
        border-color: #ffd1de; }

@keyframes rotate {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }
        .basis .main .btn.stopRecord::before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          border-radius: 50%;
          border: 3px solid #ffd1de;
          border-left: 3px solid rgba(255, 255, 255, 0);
          border-right: 3px solid rgba(255, 255, 255, 0);
          animation: rotate 3s linear infinite; }
      .basis .main .btn.download {
        background: #23ade5;
        border-color: #b5eaff; }
    .basis .main .tip {
      font-size: 12px;
      color: #b9b9b9; }
  .basis .info {
    height: 140px;
    font-size: 12px;
    color: #666; }
    .basis .info .record_config .item {
      padding: 10px;
      border-top: 1px solid #e6e6e6; }
      .basis .info .record_config .item .name {
        margin-bottom: 10px; }
      .basis .info .record_config .item .value {
        display: flex;
        align-items: center; }
        .basis .info .record_config .item .value input {
          width: 100%;
          outline: none;
          padding: 0 5px;
          height: 25px;
          line-height: 25px;
          border: 1px solid #ccc;
          background: #f7f7f7; }
    .basis .info .record_info .item {
      display: flex;
      padding: 10px;
      border-top: 1px solid #e6e6e6; }
      .basis .info .record_info .item .value {
        flex: 1;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden; }
      .basis .info .record_info .item .sub_item {
        display: flex;
        flex: 1; }
  .basis .footer {
    height: 30px;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
    letter-spacing: 2px;
    transition: all 0.3s ease;
    color: rgba(0, 0, 0, 0.5);
    border-top: 1px solid #e6e6e6;
    background: #f1f1f1; }
    .basis .footer:hover {
      color: rgba(0, 0, 0, 0.8); }

.advanced {
  display: flex;
  flex-direction: column;
  height: 100%; }
  .advanced .header {
    height: 30px;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
    letter-spacing: 2px;
    transition: all 0.3s ease;
    color: rgba(0, 0, 0, 0.5);
    border-bottom: 1px solid #e6e6e6;
    background: #f1f1f1; }
    .advanced .header:hover {
      color: rgba(0, 0, 0, 0.8); }
  .advanced .main {
    flex: 1;
    color: #666; }
    .advanced .main .item {
      padding: 10px;
      border-bottom: 1px solid #e6e6e6; }
      .advanced .main .item .name {
        font-size: 12px;
        margin-bottom: 10px; }
        .advanced .main .item .name .openDebug {
          text-decoration: none;
          color: #23ade5; }
      .advanced .main .item .value {
        display: flex;
        align-items: center; }
        .advanced .main .item .value input,
        .advanced .main .item .value select,
        .advanced .main .item .value textarea {
          width: 100%;
          outline: none;
          padding: 0 5px;
          font-size: 12px;
          height: 25px;
          line-height: 25px;
          border: 1px solid #ccc; }
        .advanced .main .item .value textarea {
          height: 50px;
          padding: 5px;
          line-height: 1.2;
          resize: none;
          background: #f7f7f7; }
  .advanced .footer img {
    max-width: 100%; }
